@charset "UTF-8";
:root {
  --clip: left center;
  --size: 0;
  --background-thumb: #2b3769;
  --scrollbar-thumb-bgcolor: #757575;
  --stripe-width: 10px;
  --nav-marker-border: #3b8686;
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
}

.masked-input {
  color: transparent; /* Скрыть текст, пока маска не активна */
}

.masked-input::placeholder {
  color: black; /* Цвет плейсхолдера */
}

.gradient-preloader {
  background: linear-gradient(90deg, #2b3769 -23.739%, #93a9ff 96.778%);
}

@media (min-width: 1440.99px) {
  .underline:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline:hover::before {
    width: 75%;
  }
  .underline:hover::after {
    width: 40%;
  }
  .underline::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: -2px;
    width: 0;
    height: 2px;
    background-color: #2b3769;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -5px;
    width: 0;
    height: 2px;
    background-color: #2b3769;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
}
@media screen and (max-width: 960px) {
  .project-page {
    padding: 0 30px;
  }
}
@media screen and (max-width: 490px) {
  .project-page {
    padding: 0 10px;
  }
}
.project-page__title {
  font-size: 65px;
  font-weight: 400;
  line-height: 100%;
  letter-spacing: 0%;
  text-align: center;
  margin-bottom: 4rem;
}
@media (min-width: 960.98px) and (max-width: 1920px) {
  .project-page__title {
    font-size: calc(24px + 41 * (100vw - 960px) / 960);
  }
}
@media screen and (max-width: 960px) {
  .project-page__title {
    font-size: 20px;
  }
}
.project-page__text {
  margin: 0 auto;
  font-family: "averta-regular", sans-serif;
  font-size: 16px;
  line-height: 125%; /* 20px/16 */
  max-width: 975px;
  text-align: center;
  margin-bottom: 7rem;
}
.project-page__text span {
  white-space: nowrap;
}
@media (min-width: 320.98px) and (max-width: 1920px) {
  .project-page__text {
    font-size: calc(12px + 4 * (100vw - 320px) / 1600);
  }
}
@media screen and (max-width: 690px) {
  .project-page__text {
    margin-bottom: 30px;
  }
}
@media screen and (max-width: 320px) {
  .project-page__text {
    font-size: 12px;
  }
}
.project-page__content {
  position: relative;
  z-index: 2;
  background: #fff;
}
@media (min-width: 960.99px) {
  .project-page__content {
    border-radius: 30px;
  }
}
@media screen and (max-width: 1140px) {
  .project-page__content {
    border-top-right-radius: 0;
    border-top-left-radius: 0;
  }
}
.project-page__footer {
  display: flex;
  justify-content: center;
}
.project-page__button {
  background: #ffffff;
  border: 1px solid transparent;
}
.project-page__button:hover {
  border: 1px solid #fff;
}
.project-page__button:hover .icon {
  transform: rotate(-90deg);
}
.project-page__button:hover span {
  color: #fff;
}
.project-page__button:hover::before {
  background: #2b3769;
}
.project-page__icon .icon-arrow-dow {
  color: #fff;
  font-size: 2.2rem;
  width: 4.8rem;
  height: 4.8rem;
}

@media (min-width: 1140.99px) {
  .tabs-button {
    margin-bottom: -3rem;
  }
}
.tabs-button__wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-end;
  gap: 10px;
}
.tabs-button__wrapper._responsive .tabs-button__item {
  display: block;
}
@media screen and (max-width: 1140px) {
  .tabs-button__wrapper {
    align-items: center;
    box-sizing: border-box;
    justify-content: flex-start;
    gap: 40px;
    padding: 0 3.4rem;
  }
}
@media screen and (max-width: 960px) {
  .tabs-button__wrapper {
    align-items: flex-start;
    flex-direction: column;
    padding: 0;
  }
}
.tabs-button__item {
  cursor: pointer;
  padding: 24px 0 31px;
  font-family: "Inter", sans-serif;
  font-size: 3.5rem;
  font-weight: 400;
  line-height: 120%; /* 42px/35 */
  white-space: nowrap;
  color: #fff;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
  transition: all 0.5s ease-in-out;
}
.tabs-button__item span {
  display: flex;
  line-height: 100%;
}
@media (min-width: 1140.99px) {
  .tabs-button__item span {
    padding: 0 3.4rem;
  }
}
.tabs-button__item:hover {
  box-shadow: 0 -1px 7px #b8b8b8;
}
@media (min-width: 1140.99px) {
  .tabs-button__item:not(.active) {
    color: #000;
    background: #f2f2f2;
    height: 128px;
  }
}
@media (min-width: 1140.98px) and (max-width: 1920px) {
  .tabs-button__item:not(.active) {
    height: calc(92px + 36 * (100vw - 1140px) / 780);
  }
}
@media screen and (max-width: 960px) {
  .tabs-button__item:not(.active) {
    display: none;
  }
}
.tabs-button__item.active {
  box-shadow: none;
  height: 100%;
  background: #2b3769;
}
.tabs-button__item:nth-child(1) {
  max-width: 128px;
}
@media (min-width: 1140.99px) {
  .tabs-button__item:nth-child(1) {
    min-width: 128px;
  }
  .tabs-button__item:nth-child(1) span {
    display: block;
    text-align: center;
  }
}
@media screen and (max-width: 960px) {
  .tabs-button__item:nth-child(1) {
    max-width: 100%;
  }
}
@media (min-width: 960.98px) and (max-width: 1920px) {
  .tabs-button__item {
    font-size: calc(16px + 19 * (100vw - 1140px) / 780);
  }
}
@media screen and (max-width: 960px) {
  .tabs-button__item {
    width: 100%;
    padding: 21px 0;
    font-size: 16px;
  }
}
@media (min-width: 960.99px) {
  .tabs-button__button {
    display: none;
  }
}
@media screen and (max-width: 960px) {
  .tabs-button__button {
    display: block;
    align-self: start;
    margin: 10px 0;
  }
  .tabs-button__button .burger-button__items span {
    background: #fff;
  }
  .tabs-button__button .burger-button__items__wrapper {
    background: transparent;
  }
}

.tab-content {
  position: relative;
  height: 100%;
}
.tab-content__items {
  display: none;
  margin-bottom: 35px;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
}
@media screen and (max-width: 1440px) {
  .tab-content__items {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }
}
@media screen and (max-width: 690px) {
  .tab-content__items {
    grid-template-columns: repeat(1, 1fr);
  }
}
.tab-content__items span {
  display: inline;
}
.tab-content__items.active {
  display: grid !important;
}
.tab-content__column {
  cursor: pointer;
  position: relative;
  display: flex;
  flex-direction: column;
  border-radius: 30px;
}
@media (min-width: 960.99px) {
  .tab-content__column:hover .tab-content__text {
    opacity: 1;
    scale: 1;
  }
}
.tab-content__image {
  cursor: pointer;
  width: 100%;
  overflow: hidden;
  margin-bottom: 5px;
}
.tab-content__image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
@media (min-width: 690.98px) {
  .tab-content__image {
    pointer-events: none;
  }
}
.tab-content__footer {
  flex: 1 1 auto;
  display: flex;
  align-items: center;
  gap: 10px;
  justify-content: space-between;
}
@media (min-width: 1440.98px) and (max-width: 1740px) {
  .tab-content__footer {
    flex-direction: column;
    gap: 20px;
  }
}
@media screen and (max-width: 1164px) {
  .tab-content__footer {
    flex-direction: column;
    gap: 20px;
  }
}
.tab-content__text {
  pointer-events: none;
  color: #fff;
  font-size: 16px;
  font-weight: 400;
  line-height: 125%; /* 20px/16 */
  text-align: left;
  flex: 1 1 auto;
  display: none;
}
@media screen and (max-width: 1164px) {
  .tab-content__text {
    max-width: 100%;
    width: 100%;
  }
}
@media (min-width: 320.98px) and (max-width: 1920px) {
  .tab-content__text {
    font-size: calc(12px + 4 * (100vw - 320px) / 1600);
  }
}
@media (min-width: 960.99px) {
  .tab-content__text {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    background: rgba(60, 59, 81, 0.8078431373);
    scale: 0;
    opacity: 0;
    transition: opacity 0.4s, scale 0.4s ease 0s;
  }
  .tab-content__text span {
    margin-bottom: 6rem;
  }
}
@media screen and (min-width: 960.99px) and (max-width: 1024px) {
  .tab-content__text span {
    margin-bottom: 4rem;
  }
}
@media screen and (max-width: 960px) {
  .tab-content__text {
    display: block;
    position: relative;
  }
}
@media screen and (max-width: 320px) {
  .tab-content__text {
    font-size: 12px;
  }
}
.tab-content__button {
  right: 0;
  bottom: 0;
  background: #2b3769;
  border: 1px solid transparent;
}
.tab-content__button span {
  color: #fff;
}
.tab-content__button:hover .icon {
  transform: rotate(-45deg);
  background: #2b3769;
}
.tab-content__button:hover ._icon::before {
  color: #fff;
  font-weight: 400;
}
.tab-content__button:hover span {
  color: #000;
}
.tab-content__button:hover::before {
  background: #fff;
  color: #fff;
}
.tab-content__button .icon {
  background: #fff;
}
.tab-content__button ._icon {
  border-radius: 50%;
  color: #2b3769;
}
.tab-content__button ._icon::before {
  font-weight: 600;
}
@media screen and (max-width: 960px) {
  .tab-content__button {
    width: 268px;
  }
}
.tab-content__icon {
  pointer-events: all;
  width: 4.8rem;
  height: 4.8rem;
  border-radius: 50%;
  background: #fff;
  transition: all 0.3s ease-in-out;
}
@media screen and (max-width: 960px) {
  .tab-content__icon {
    display: none;
  }
}
.tab-content__icon ._icon::before {
  color: #2b3769;
  font-size: 18px;
  font-weight: 600;
}

.button__icon {
  color: #fff;
}

