}
.request-audit__phone {
  font-size: 40px;
  font-weight: 400;
  line-height: 100%;
  text-align: center;
}
@media (min-width: 320.98px) and (max-width: 1920px) {
  .request-audit__phone {
    font-size: calc(20px + 20 * (100vw - 320px) / 1600);
  }
}
@media screen and (max-width: 320px) {
  .request-audit__phone {
    font-size: 20px;
  }
}

.audit-form__column {
  box-sizing: border-box;
  border-radius: 30px;
  overflow: hidden;
  align-self: center;
}
@media screen and (max-width: 960px) {
  .audit-form__column {
    max-width: 268px;
    width: 100%;
  }
}
@media screen and (max-width: 490px) {
  .audit-form__column {
    max-width: 238px;
